
.about-container{
    width: 100%;
    margin: auto;
    height: 600px;
}
.about-heading{
   
        font-size: 23px;
        margin-top: 20px;
     font-weight: bold;

}
.about-data-container{

    display: flex;
    /* border:1px solid red; */
    justify-content: center;
    width: 80%;
    margin: auto;
    margin-top: 30px;
   
    height:400px;
    gap: 20px;
}
.about-text{

   
    text-align: left;
    padding:20px;
    
    margin: auto;
    width: 70%;
    /* border: 1px solid red; */
    font-size: 18px;
   
    
}

.about-text-heading{
    font-size: 24px;
    font-weight: bold;
}

.about-image{
    width:30%;
    margin: auto;
    /* border-radius: 50%; */
}
.about-image>img{
    width:280px;
    height:300px; 
    border-radius: 50%;
}


@media screen  and (min-width:450px) and (max-width:720px){
    
    .about-data-container{

        display: flex;
        flex-direction: column-reverse;
        /* border:1px solid red; */
        justify-content: center;
        width: 80%;
        margin: auto;
        margin-top: 30px;
       
        height:400px;
        gap: 20px;
    }

    .about-text{

   
        text-align: left;
        padding:20px;
        
        margin: auto;
        width: 70%;
        /* border: 1px solid red; */
        font-size: 16px;
       
        
    }
    
    .about-text-heading{
        font-size: 18px;
        font-weight: bold;
    }

    /* .about-image{
        height: 100px;
        width: 100px;
        margin: auto;
        /* border-radius: 50%; */
    /* } */ 
    .about-image>img{
        height:150px;
        width:150px;
        margin: auto;
        border-radius: 50%;
    }
    
}


@media screen  and (min-width:100px) and (max-width:450px){
    
    .about-data-container{

        display: flex;
        flex-direction: column-reverse;
        /* border:1px solid red; */
        justify-content: center;
        width: 80%;
        margin: auto;
        margin-top: 30px;
       
        height:400px;
        gap: 20px;
    }

    .about-text{

   
        text-align: left;
        
      
        
        margin: auto;
       
        width: 80%;
        /* border: 1px solid red; */
        font-size: 14px;
       
        
    }
    
    .about-text-heading{
        font-size: 16px;
        font-weight: bold;
    }
    .about-image{
        margin-top: 45px;
    }

    .about-image>img{
        height:100px;
        width:100px;
        margin: auto;
        border-radius: 50%;
    }
    
}