
.skill-heading{
    font-size: 23px;
    font-weight: bold;
   }
   
   .skill-container{
       
       display: grid;
       width:80%;
       margin:auto;
       margin-top: 80px;
   
   
       grid-template-columns: repeat(5,1fr);
       grid-template-rows: auto;
        
       gap:60px
   }
   .skill-container>div{
       margin: auto;
       height:100px;
       width:100px
   }
   .skill-container img{
   
       height:100;
       width:100px
   }
   
   @media screen and (min-width:450px) and (max-width:720px) {
       
       .skill-container{
       
           display: grid;
           width:80%;
           margin:auto;
           margin-top: 80px;
       
       
           grid-template-columns: repeat(3,1fr);
           grid-template-rows: auto;
           margin-left: 70px;
            
           gap:20px
       }
   
       .skill-container>div{
           margin: auto;
           height:80px;
           width:80px
       }
       .skill-container img{
       
           height:80px;
           width:80px;
       }
   
   }
   
   
   @media screen and (min-width:100px) and (max-width:450px) {
       
       .skill-container{
       
           display: grid;
           width:80%;
           margin:auto;
           margin-top: 80px;
       
       
           grid-template-columns: repeat(2,1fr);
           grid-template-rows: auto;
           margin-left: 60px;
            
           gap:20px
       }
   
       .skill-container>div{
           margin: auto;
           height:60px;
           width:60px
       }
       .skill-container img{
       
           height:60px;
           width:60px;
       }
   
   }