.nav-container{
    /* border: 1px solid red; */
    width: 100%;
    display: flex;
    /* justify-content: space-between; */
    align-items: center;
    height: 80px;
    /* margin-top: 150px; */
    position: fixed;
    top:1px;
    /* border:1px solid black; */
    /* background-color: #06d6a0; */
    background-color:  #e2d80d;
    font-weight: 700;
    letter-spacing: 1px;
  
  }
  .nav-Image{
    /* border:1px solid black; */
    width: 20%;
    font-size: 25px;
    color:black;
    text-align: center;
    font-weight: bold;
  }
  
  
  .theme-button{
    color:black
  }
  
  .nav-Links {
    display: flex;
    /* border:1px solid black; */
    width: 75%;
    /* border:1px solid black; */
    align-items: center;
    font-size: 19px;
    height: 50px;
    /* align-content: center; */
  
   
    justify-content: flex-end;
    gap: 20px;
    /* padding-right:50px; */
  }
  
   .list{
    display: none;
  } 
  
  
  @media screen and (max-width:720px) and (min-width:450px) {
    
  .nav-container{
    height: 40px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-right: 30px;
  
  }
  .nav-Image{
    font-size: 16px;
  }
  
  .nav-Links{
    display: none;
  }
  .list{
    display: block;
    margin-right: 50px;
  }
  
  
  .nav-Image{
    /* border:1px solid black; */
    width: 20%;
    font-size: 16px;
   
    color:black;
    text-align: center;
    font-weight: bold;
    margin-left: 10px;
  }
  
  
  }
  
  @media screen and (max-width:450px) and (min-width:100px) {
    
    .nav-container{
      height: 50px;
      width: 100%;
      display: flex;
      justify-content: space-between;
      padding-right: 30px;
    
    }
    .nav-Image{
      font-size: 16px;
      margin-left: 40px;
    }
    
    .nav-Links{
      display: none;
    }
    .list{
      display: block;
      margin-right: 80px;
      font-size: 16px;
      color:black;
    }
  
    .nav-Image{
      /* border:1px solid black; */
      width: 20%;
      font-size: 14px;
     
      color:black;
      text-align: center;
      font-weight: bold;
      margin-left: 40px;
    }
    
    
    }