
.project-main-container{
    width: 100%;
    height: 900px;
    margin: auto;
    margin-top: 240px;
 
}


.project-heading{
    font-size: 30px;
 font-weight: bold;

}

.project-container{
    width:80%;
    margin:auto;
    margin-top: 40px;
    padding:20px;
    display: grid;
   
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: auto;
    font-size: 14px;
    font-weight: 550;
    

    gap: 30px;
    
   


}

.project-single-div{
    margin: auto;
    padding: 20px;
    background-color: #e0e5e9;
    color: black;
   
    border-radius: 15px;
    /* height:380px; changed this for 2 projects */
   

    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.project-single-div:hover{

    transform: scale(1.1);
}

.project-desc{
    margin-top: 15px;
    height:50px;
    text-align: center;
}



.project-image{
    
    display: block;
    margin: auto;
    margin-top: 20px;
   
}


.git-icon img{
    height: 20px;
    width:20px
    }
    .icon-div{
        width:50%;
        margin: auto;
        margin-top: 20px;
        display: flex;
        justify-content: center;
     
        align-items: center;
       
        gap:30px;
    }

    .web-link img{
      
        height: 20px;
        width:20px
    }
    .tech-stacks{
        display: grid;
       
        grid-template-columns: repeat(5,1fr);
        width: 80%;
        margin: auto;
        margin-top:15px
        /* gap:10px; */

    }
    .tech-stacks img{
        width:25px;
        height:25px

    }

    .tech-stacks-heading{
        font-weight: bold;
        margin-top: 15px;
    }

    @media screen and (min-width:450px) and (max-width:720px){

        .project-container{
           
            width: 80%;
            margin: auto;
            margin-top: 20px;
            display: grid;
            grid-template-columns: repeat(2,1fr);
            grid-template-rows: auto;
            font-size: 14px;
            margin-left: 90px;
        }

        .tech-stacks{
            display: grid;
            width: 80%;
            margin: auto;
           
            grid-template-columns: repeat(5,1fr);
          
            margin-top:50px
            /* gap:10px; */
    
        }
        .project-heading{
            /* text-align: center; */
            font-size: 24px;
         font-weight: bold;
         margin-left: 120px;
        
        }
        
    }


    
    @media screen and (min-width:100px) and (max-width:450px){

        .project-container{
            width: 80%;
           
            margin: auto;
           
            margin-top: 20px;
            display: grid;
            grid-template-columns: repeat(1,1fr);
            grid-template-rows: auto;
            font-size: 14px;
            margin-left: 50px;
        }
        .tech-stacks{
            display: grid;
           
            grid-template-columns: repeat(5,1fr);
            width: 80%;
            margin: auto;
            margin-top:10px
            /* gap:10px; */
    
        }
       
     } 