
.contact-main-container{
    margin-top: 200px;
}
.contact-main-heading{
    font-size: 30px;
    font-weight: bold;
}
.get-in-touch-green{
    color:  #e2d80d;
    font-weight: bold;
}

.contact-container{
   
    /* border: 1px solid red; */
    width: 80%;
    margin: auto;
    margin-top: 20px;
    display: flex;
    
    gap:100px

}

.get-in-touch-left{
    font-size: 21px;
    font-weight: bold;
}

.contact-icons-div{


    margin-top: 30px;
    display: flex;
    font-size: 16px;
    gap:30px;
    align-items: center;
    

}
.right{
    margin-top:10px ;
    width:40%;
    border: 1px solid  #e2d80d;
    border-radius: 10px;
    padding:10px;
}
.right input{
    border: 1px solid  #e2d80d;

}
.right-heading{
    font-size: 21px;
    font-weight: bold;
}
.name-email-div{
    display: flex;
    width: 100%;
    gap:30px;
   
}
/* .name-email-div>div{
    width: 40%;
    margin: auto;
} */
.input-email{
    width:58%

}

.input-name{
    width: 30%;
    margin-left: 13px;
}
.input-subject{
    width: 95%;
    margin: auto;
    margin-top: 15px;
}
.form{
margin-top: 20px;
}
.text-area>textarea{
    border: 1px solid  #e2d80d;
    border-radius: 10px;
    width:95%;
    margin: auto;
    margin-top: 15px;
}

@media screen and (min-width:450px) and (max-width:720px) {
    
    .contact-container{
        
        flex-wrap: wrap;
        /* border: 1px solid red; */
       
        width: 80%;
        margin: auto;
       
        display: flex;
        
        gap:100px
    
    }

    .right{
        
        width:95%;
        margin: auto;
        margin-top:10px ;
        border: 1px solid  #e2d80d;
        border-radius: 10px;
        padding:10px;
    }

}

@media screen and (min-width:100px) and (max-width:450px) {
    
    .contact-container{
        display: flex;
        flex-wrap: wrap;
        /* border: 1px solid red; */
        width: 80%;
        margin: auto;
        
        
        gap:100px
    
    }

    .right{
        
        width:95%;
        margin: auto;
        margin-top:10px ;
        border: 1px solid  #e2d80d;
        border-radius: 10px;
        padding:10px;
    }

}