 }  */
 .home-hi{
    font-size: 30px;
 }
.home-data-container{

    display: flex;
    /* border:1px solid red; */
    justify-content: center;
    width: 80%;
    margin: auto;
    margin-top:100px;
    height:400px;
    gap: 70px;
}
.home-text{

    font-size: 24px;
    text-align: center;
    margin: auto;
    width: 40%;
    /* border: 1px solid red; */
    font-weight: bold;
    
}

.home-image{
    width:40%;
    margin: auto;
    /* border-radius: 50%; */
}
.home-image>img{
    width:280px;
    height:300px; 
    border-radius: 50%;
}
.type-writer{
    color: #e2d80d;
    font-size:28px
}
.my-name{
    font-size: 35px;
}

/* .resume-link{
background-color: #06d6a0;
} */

@media screen and (min-width:450px) and (max-width:720px) {
    

    .home-data-container{

        display: flex;
        /* border:1px solid red; */
        justify-content: center;
        width: 80%;
        margin: auto;
        margin-top:100px;
        margin-left: 50px;
        height:300px;
        gap: 40px;
    }

    .home-text{

        font-size: 18px;
        text-align: center;
        margin: auto;
        width: 40%;
        /* border: 1px solid red; */
        font-weight: bold;
        
    }

    .type-writer{
        color: #e2d80d;
        font-size:16px
    }
    .my-name{
        font-size: 20px;
    }

    .home-hi{
        font-size: 20px;
     }
    
     .home-image>img{
        width:150px;
        height:200px; 
        border-radius: 50%;
    }

}

